/* Style the input field */
.inputCustom {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 2px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.2s ease-in-out;
}

/* Add focus styles */
.inputCustom:focus {
  border-color: #108A00; /* Change to your desired focus color */
  outline: none;
  box-shadow: 0 0 6px rgba(0, 255, 55, 0.397);
}

/* Style the placeholder text */
.inputCustom::placeholder {
  color: #999;
}


